.main-nav {
  position: absolute;
  width: 100%;
  padding: 6px 10px; }
  .main-nav--activated {
    position: static;
    order: -1;
    padding: 0; }

.main-nav__toggle-line {
  position: relative;
  width: 23px;
  height: 4px;
  margin-bottom: 4px;
  background-color: #81b3d2;
  transition: opacity 0.3s, transform 0.3s; }
  .main-nav__toggle-line:nth-child(3) {
    margin-bottom: 0; }

.main-nav__toggle {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 43px;
  height: 41px;
  padding: 0;
  margin-left: auto;
  border: none;
  background-color: transparent;
  outline: 0;
  user-select: none;
  cursor: pointer; }
  .main-nav__toggle:hover .main-nav__toggle-line {
    background-color: rgba(129, 179, 210, 0.6); }
  .main-nav__toggle:active .main-nav__toggle-line {
    background-color: rgba(129, 179, 210, 0.3); }
  .main-nav__toggle--activated {
    position: absolute;
    z-index: 2;
    right: 10px;
    top: 7px; }
    .main-nav__toggle--activated .main-nav__toggle-line {
      background-color: #ffffff; }
    .main-nav__toggle--activated .main-nav__toggle-line:nth-child(1) {
      top: 6px;
      margin: 0;
      transform: rotate(45deg); }
    .main-nav__toggle--activated .main-nav__toggle-line:nth-child(2) {
      opacity: 0; }
    .main-nav__toggle--activated .main-nav__toggle-line:nth-child(3) {
      top: -6px;
      transform: rotate(-45deg); }
    .main-nav__toggle--activated:hover .main-nav__toggle-line {
      background-color: #cde1ed; }
    .main-nav__toggle--activated:active .main-nav__toggle-line {
      background-color: #a7cae0; }

@media (min-width: 768px) {
  .main-nav {
    position: static; }
  .main-nav__toggle {
    display: none; } }

